___CSS_LOADER_EXPORT___.push([module.id, "#age-source-serial[data-v-6878c0be]{height:265px}", "",{"version":3,"sources":["webpack://./resources/js/admin/Components/PageSite/AgeSource.vue"],"names":[],"mappings":"AA2FA,oCACA,YACA","sourcesContent":["<template>\n  <section class=\"panel h-100-c\">\n    <header class=\"panel-heading\">\n      <h2 class=\"h5 float-left\">{{ array_get(trans, 'module.age_source') }}</h2>\n    </header>\n    <div class=\"panel-wrapper\"></div>\n    <div class=\"panel-body pb-3\">\n      <div id=\"age-source-serial\"></div>\n    </div>\n  </section>\n</template>\n\n<script>\nimport 'amcharts3';\nimport 'amcharts3/amcharts/serial';\nimport 'amcharts3/amcharts/themes/light';\n\nexport default {\n  name: \"AgeSource\",\n  props: {\n    'urls': Object,\n    'trans': Object,\n    'days': {\n      type: Number,\n      default: 30\n    }\n  },\n  data() {\n    return {\n      serialChart: null,\n      ageSource: []\n    }\n  },\n  mounted: function () {\n    this.createAmSerial();\n  },\n  watch: {\n    ageSource: function (afterVal, beforeVal) {\n      if (JSON.stringify(afterVal) !== JSON.stringify(beforeVal)) {\n        this.serialChart.dataProvider = afterVal;\n        this.$nextTick(() => {\n          this.serialChart.validateData();\n          this.serialChart.animateAgain();\n        });\n      }\n    }\n  },\n  methods: {\n    createAmSerial: function () {\n      this.serialChart = AmCharts.makeChart(\"age-source-serial\", {\n        \"hideCredits\":true,\n        \"type\": \"serial\",\n        \"theme\": \"light\",\n        \"dataProvider\": this.ageSource,\n        \"valueAxes\": [{\"gridColor\": \"#878787\", \"gridAlpha\": 0.2, \"dashLength\": 0, \"color\": \"#878787\"}],\n        \"gridAboveGraphs\": true,\n        \"startDuration\": 1,\n        \"graphs\": [{\n          \"balloonText\": \"[[category]]: <b>[[value]]</b>\",\n          \"fillAlphas\": 0.8,\n          \"lineAlpha\": 0.2,\n          \"type\": \"column\",\n          \"valueField\": \"visits\",\n          \"color\": \"#878787\"\n        }],\n        \"chartCursor\": {\"categoryBalloonEnabled\": false, \"cursorAlpha\": 0, \"zoomable\": false},\n        \"categoryField\": \"age\",\n        \"categoryAxis\": {\n          \"gridPosition\": \"start\",\n          \"gridAlpha\": 0,\n          \"tickPosition\": \"start\",\n          \"tickLength\": 20,\n          \"color\": \"#878787\"\n        },\n        \"export\": {\"enabled\": false}\n      });\n      this.updateAgeSource();\n    },\n    updateAgeSource: function () {\n      if (this.filled(this.urls.age_source)) {\n        axios.get(this.urls.age_source + '?days=' + this.days)\n          .then((response) => {\n            this.ageSource = response.data;\n          });\n      }\n    }\n  }\n}\n</script>\n\n<style scoped>\n#age-source-serial {\n  height: 265px;\n}\n</style>\n"],"sourceRoot":""}]);
